.home {
  width: 100%;
  height: 100vh;
  position: relative;
}

.KSP_video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.banner {
  width: 100%;
  height: 55%;
}

.KSP_image {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 60%;
  display: flex;
  justify-content: start;
  align-items: end;
}

.KSP_image_logo {
  width: 40%;
  height: 40%;
}

