.main {
  text-align: center;
  margin-top: 20px;
  opacity: 0; /* Start with opacity 0 to hide the element initially */
  animation: slideInFromLeft 1s ease-in-out forwards; /* Apply the animation */
}

.main h2 {
  font-size: 3rem;
  font-weight: bold;
  color: #155adac4;
}

@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  5% {
    opacity: 0;
    transform: translateX(-50%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@media screen and (max-width: 850px) {
  .banner {
    width: 100%;
    height: 30%;
    position: relative;
  }

  .KSP_video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .KSP_image {
    position: absolute;
    top: calc(100% - 100px); /* Adjust the value as needed */
    left: 0;
    width: 40%;
    height: 100px;
    display: flex;
  }
  .KSP_image_logo {
    width: 100%;
    height: 100%;
  }
}
