.nav-links a {
  text-decoration: none;
  color: #fff;
  padding: 0.7rem 1rem;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
}

.nav-links a::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease-in-out;
}

.nav-links a:hover::before {
  transform: scaleX(1);
}
.fa-bars,
.fa-times {
  color: #fff;
}
.menu-icons {
  display: none;
}
@media screen and (max-width: 850px) {
  .navbar {
    z-index: 99;
  }
  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: rgba(255, 255, 255, 0.25);
    box-shadow: 0 8 32 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(4px);
    border-radius: 13px;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: -110%;
    align-items: stretch;
    padding: 80px 0 30px 0;
    margin: 0;
    z-index: 1;
    transition: all 0.5s ease-in-out;
  }
  .nav-menu.active {
    left: 0%;
  }
  .nav-links a {
    display: block;
    widows: 100%;
    padding: 2rem 0;
    color: #222;
  }
  .nav-links a:hover {
    background-color: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(20px);
  }
  .menu-icons {
    display: block;
  }
}
